<template>
  <div class='header'>
    <h1 class='title' ref='title'>Header</h1>

    <div class='quad1' ref='quad1'></div>
    <div class='quad2' ref='quad2'></div>
    <div class='quad3' ref='quad3'></div>
  </div>
</template>

<script>
import { gsap } from 'gsap'
import { ScrollTrigger } from 'gsap/ScrollTrigger'
gsap.registerPlugin(ScrollTrigger)

export default {
  mounted () {
    gsap.timeline({
      scrollTrigger: {
        trigger: '#header',
        start: 'top',
        // markers: true,
        scrub: true
      }
    })
      .fromTo(this.$refs.quad2, { y: 0 }, { y: 90 }, 0)
      .fromTo(this.$refs.quad3, { y: 0 }, { y: 90 }, 0)
      .fromTo(this.$refs.title, { y: 0 }, { y: 400 }, 0)
  }
}
</script>
<style lang='scss' scoped>
  .header {
    background-color: wheat;
    width: 100%;
    height: 800px;
    color: white;
    position: relative;
    * { position: absolute; }
    .title {
      width: 100%;
      text-align: center;
      z-index: 1;
    }
    .quad {
      &1 {
        width: 100%;
        height: 150px;
        background-color: violet;
        bottom: 0;
        z-index: 4;
      }
      &2 {
        width: 200px;
        height: 100px;
        background-color: red;
        bottom: 150px;
        right: 0;
        z-index: 3;
      }
      &3 {
        width: 100px;
        height: 100px;
        bottom: 150px;
        background-color: blue;
        z-index: 2;
      }
    }
  }
</style>
